
import { defineComponent, reactive } from "vue";
import CustomTemplate from "@/apps/HTS/views/reports/templates/CustomTemplate.vue"
import WeeklyTemplate from "@/apps/HTS/views/reports/templates/WeeklyTemplate.vue"
import { IonPage } from "@ionic/vue"
import { Service } from "@/services/service";
import { HtsReportService } from '@/apps/HTS/services/hts_report_service';

export default defineComponent({
   components: {
      IonPage,
      CustomTemplate,
      WeeklyTemplate
   },
   setup() {
      const info = reactive({
         'health_facility': Service.getLocationName(),
         'compiled_by': Service.getUserName()
      } as Record<string, any>)
      const report = new HtsReportService('HTS HIV TESTING SUMMARY')
      return {
         info,
         indicators: {},
         drill: (indicator: string) => {
            console.log(indicator)
         },
         params: {
            title: 'HTS Weekly Summary',
            logo: 'reports.png',
            cssFile: "/assets/css/hts-report-style-print.css",
            hasPeriodSelection: true,
            data: async (r: any, f: any, computedData: any) => {
               report.startDate = computedData.start_date
               report.endDate = computedData.end_date
               return report.requestReport()            }
         }
      }
   }
})
